:root {
  --bg-1: #fffafc;
  --bg-2: #fff5f9;
  --card: #fff;
  --accent: #ff6f91;
  --muted: #6c6c6c;
  --shadow: 0 20px 50px #1e14280f;
  --max-width: 900px;
}

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  background: linear-gradient(180deg, var(--bg-1), var(--bg-2));
  color: #222;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 36px;
  font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Arial;
  display: flex;
}

.page {
  width: 100%;
  max-width: var(--max-width);
  padding: 12px;
}

.card {
  background: var(--card);
  box-shadow: var(--shadow);
  text-align: center;
  border-radius: 16px;
  padding: 22px;
  position: relative;
  overflow: visible;
}

.bg-deco {
  opacity: .12;
  pointer-events: none;
  filter: blur(2px);
  background-position: center;
  background-size: cover;
  border-radius: 10px;
  width: 140px;
  height: 110px;
  position: absolute;
  top: 12px;
  right: 22px;
}

.controls-top {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 6px;
  display: flex;
}

.candle-controls {
  align-items: center;
  gap: 8px;
  display: flex;
}

.candle-controls input[type="range"] {
  width: 220px;
}

.candle-controls button {
  cursor: pointer;
  background: #fff0f5;
  border: 1px solid #ffd6e6;
  border-radius: 8px;
  padding: 6px 10px;
}

.title {
  margin: 0;
  font-size: 20px;
  font-weight: 700;
}

.cake-wrap {
  cursor: pointer;
  outline: none;
  width: 100%;
  max-width: 640px;
  margin: 14px auto;
}

.cake-svg {
  width: 100%;
  height: auto;
  display: block;
}

.controls {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-top: 14px;
  display: flex;
}

button {
  background: var(--accent);
  color: #fff;
  cursor: pointer;
  border: 0;
  border-radius: 12px;
  padding: 10px 18px;
  font-weight: 700;
  box-shadow: 0 8px 22px #ff6f911f;
}

button.secondary {
  color: #222;
  box-shadow: none;
  background: #fff;
  border: 1px solid #eee;
}

.hint {
  color: var(--muted);
  margin-top: 10px;
  font-size: 13px;
}

kbd {
  background: #f3f3f6;
  border-radius: 6px;
  padding: 3px 6px;
  font-family: monospace;
  font-size: 12px;
}

.candle .flame {
  transform-origin: 50% 12px;
  filter: drop-shadow(0 6px 12px #ff8c781a);
  transition: transform .14s linear, opacity .2s linear;
}

.candle .flameGlow {
  transition: transform .2s linear, opacity .16s linear;
}

.candle-out .candle .flame, .candle-out .candle .flameGlow {
  opacity: 0;
  transform: scale(.25)translateY(6px);
}

.candle-on .candle .flame, .candle-on .candle .flameGlow {
  opacity: 1;
  transform: scale(1)translateY(0);
}

.conf {
  transform-origin: center;
  transition: transform .9s cubic-bezier(.17, .84, .44, 1), opacity .9s;
}

@media (width <= 520px) {
  .bg-deco {
    display: none;
  }

  .card {
    padding: 16px;
  }

  button {
    padding: 8px 12px;
    font-size: 14px;
  }

  .candle-controls input[type="range"] {
    width: 140px;
  }
}
